
const gatherIndexJS = require.context(
  //componentsindex.js
  './components', true, /\index.js$/
)

const components = {}
//index.js
gatherIndexJS.keys().forEach(fileName => {
  let list = gatherIndexJS(fileName);
  for (var val in list) {
    components[val] = list[val]
  }
});
delete components.default

export default components
